import React, { PropTypes } from "react";

const NotFound = props => {
  return (
    <div>
      <h1 className="display-4">
        <span className="text-danger">404</span> Page Not Found
      </h1>
      <p className="lead">Sorry,that page does not exist</p>
    </div>
  );
};

NotFound.propTypes = {};

export default NotFound;
